// App.js

import React, { useState } from 'react';
import './App.css';
import DatePicker from 'react-datepicker'; // DatePicker 추가
import 'react-datepicker/dist/react-datepicker.css';
import ko from 'date-fns/locale/ko';
import CityInput from './CityInput.js';
import buildSkyscannerLink from './SkyscannerUtils'; // SkyscannerUtils.js를 import
import buildNaverTripLink from './NaverTripUtils';
import buildAirbnbLink from './AirbnbUtils.js';
import buildKlookLink from './KlookUtils.js';
import buildTripadLink from './TripadUtils.js';
import 'typeface-roboto';





function App() {
  const [flightCost, setFlightCost] = useState("");
  const [accommodationCost, setAccommodationCost] = useState("");
  const [mealCost, setMealCost] = useState("");
  const [transportationCost, setTransportationCost] = useState("");
  const [activitiesCost, setActivitiesCost] = useState("");
  const [totalCost, setTotalCost] = useState(0);
  const [numberOfPeople, setNumberOfPeople] = useState(1); // 인원 수 기본값 1로 설정
  const [departureDate, setDepartureDate] = useState(null); // 출발일
  const [arrivalDate, setArrivalDate] = useState(null); // 도착일
  const [departureCity, setDepartureCity] = useState(''); // 출발도시
  const [arrivalCity, setArrivalCity] = useState(''); // 도착도시
  
  // 스카이스캐너 페이지로 이동하는 함수
  const goToSkyscanner = () => {
    const link = buildSkyscannerLink({
      departureCity,
      arrivalCity,
      departureDate,
      arrivalDate,
      numberOfPeople,
    });

    if (!departureCity || !arrivalCity || !departureDate || !arrivalDate || !numberOfPeople) {
      window.open('https://www.skyscanner.co.kr/', '_blank');
    } else {
      // 값이 모두 채워져 있다면 생성한 링크로 이동
      window.open(link, '_blank');
    }
  };

  const calculateTotalCost = () => {
    const total = Number(flightCost) + Number(accommodationCost) + Number(mealCost) + Number(transportationCost) + Number(activitiesCost);
    setTotalCost(total);
  };

  
  //네이버 항공권 페이지로 이동하는 함수
  const goToNaverTrip = () => {
    const link = buildNaverTripLink({
      departureCity,
      arrivalCity,
      departureDate,
      arrivalDate,
      numberOfPeople,
    });

    if (!departureCity || !arrivalCity || !departureDate || !arrivalDate || !numberOfPeople) {
      window.open('https://flight.naver.com/', '_blank');
    } else {
      // 값이 모두 채워져 있다면 생성한 링크로 이동
      window.open(link, '_blank');
    }
  };

  //아고다 페이지로 이동하는 함수
  const goToAgoda = () => {
    window.open('http://agoda.com/', '_blank');
  }

  const skyrent = () => {
    window.open('https://www.skyscanner.co.kr/car-hire', '_blank');
  }

  const klookrent = () => {
    window.open('https://www.klook.com/ko/car-rentals/?des_id=10015','_blank');
  }

  const transpass = () => {
    window.open('https://www.klook.com/ko/transport/ttd/metro-passes-cards/?spm=Mobility_Aggregate.Transportation_LIST&clickId=091e95e7cd','_blank');
  }

  //에어비엔비로 이동하는 함수
  const goToAirbnb = () => {
    const link = buildAirbnbLink({
      departureCity,
      arrivalCity,
      departureDate,
      arrivalDate,
      numberOfPeople,
    });

    if (!departureCity || !arrivalCity || !departureDate || !arrivalDate || !numberOfPeople) {
      window.open('https://www.airbnb.co.kr/', '_blank');
    } else {
      // 값이 모두 채워져 있다면 생성한 링크로 이동
      window.open(link, '_blank');
    }
  };

  const goToKlook = () => {
    const link = buildKlookLink(arrivalCity);  
    if(!arrivalCity){
      window.open('https://www.klook.com/ko/?spm=SearchResult.TopNavigation.BackHome&clickId=e4d6994091', '_blank');
    } 
    else{
      window.open(link, '_blank');
    }
  }

  const goToTripad = () => {
    const link = buildTripadLink(arrivalCity);  
    if(!arrivalCity){
      window.open('https://www.tripadvisor.co.kr/', '_blank');
    } 
    else{
      window.open(link, '_blank');
    }
  }



  //n빵
  const calculateCostPerPerson = () => {
    const costPerPerson = totalCost / numberOfPeople;
    return costPerPerson;
  };

  return (
    <div className="App">
      <h1>Trip pay</h1>
      

      <div>
        <label>인원 수: </label>
        <input type="number" value={numberOfPeople} onChange={(e) => setNumberOfPeople(e.target.value)} />
      </div>

       {/* 출발일 선택 */}
       <div>
        <label>출발일: </label>
        <DatePicker selected={departureDate} onChange={(date) => setDepartureDate(date)} dateFormat="yyyy-MM-dd" locale={ko}
      />
      </div>

      {/* 도착일 선택 */}
      <div>
        <label>도착일: </label>
        <DatePicker selected={arrivalDate} onChange={(date) => setArrivalDate(date)} dateFormat="yyyy-MM-dd" locale={ko}
      />
      </div>

      <CityInput label="출발지" value={departureCity} onChange={setDepartureCity} />

      <CityInput label="도착지" value={arrivalCity} onChange={setArrivalCity} />
      

      <div>
        <label>항공권 비용: </label>
        <input type="text" value={flightCost} onChange={(e) => setFlightCost(e.target.value)} />
        <label>원</label>

        {/* 첫 번째 버튼 */}
        <button onClick={goToSkyscanner}>Skyscanner</button>

        {/* 두 번째 버튼 */}
        <button onClick={goToNaverTrip}>네이버 항공권</button>
      </div>

      <div>
        <label>숙박 비용: </label>
        <input type="text" value={accommodationCost} onChange={(e) => setAccommodationCost(e.target.value)} />
        <label>원</label>

        {/* 세번째 버튼*/}
        <button onClick={goToAgoda}>Agoda</button>

        {/* 네번째 버튼*/}
        <button onClick={goToAirbnb}>Airbnb</button>
      </div>

      <div>
        <label>식사 비용: </label>
        <input type="text" value={mealCost} onChange={(e) => setMealCost(e.target.value)} />
        <label>원</label>
        <button onClick={goToTripad}>Tripadvisor</button>
      </div>

      <div>
        <label>교통 비용: </label>
        <input type="text" value={transportationCost} onChange={(e) => setTransportationCost(e.target.value)} />
        <label>원</label>
        {/* 다섯번째 버튼 */}
        <button onClick={skyrent}>sky렌트</button>
        <button onClick={klookrent}>클룩렌트</button>
        <button onClick={transpass}>교통패스</button>
      </div>

      <div>
        <label>활동 및 엔터테인먼트 비용: </label>
        <input type="text" value={activitiesCost} onChange={(e) => setActivitiesCost(e.target.value)} />
        <label>원</label>
        <button onClick={goToKlook}>클룩</button>
      </div>

      <button onClick={calculateTotalCost}>총 비용 계산</button>

        {/* 총 비용 출력 시 가격 가독성 개선 */}
        {totalCost > 0 && (
        <p>
          총 여행 비용: {totalCost.toLocaleString()}원
        </p>
      )}

        {totalCost > 0 && (
        <p>
          인원당 비용: {calculateCostPerPerson().toLocaleString()}원
        </p>
      )}
    </div>
  );
}

export default App;


