

const buildAirbnbLink = ({ departureCity, arrivalCity, departureDate, arrivalDate, numberOfPeople }) => {
    // 기본 링크
    let link = 'https://www.airbnb.co.kr/';
    link += 's/';
  
  
    // arrivalCity에 따라 도착지 코드 설정
    const arrivalMatch = arrivalCity.match(/([가-힣]+)/);
    const arrivalCode = arrivalMatch[1];
    link += `${arrivalCode}`;
    link += '/homes?tab_id=home_tab&refinement_paths%5B%5D=%2Fhomes&flexible_trip_lengths%5B%5D=channel=EXPLORE&date_picker_type=calendar&checkin=';
  
    // 체크인하는 날
    if (departureDate) {
        const year = departureDate.getFullYear().toString();
        const month = (departureDate.getMonth() + 1).toString().padStart(2, '0');
        const day = departureDate.getDate().toString().padStart(2, '0');
      
        const formattedDepartureDate = `${year}-${month}-${day}`;
        link += formattedDepartureDate;
      } else {
        // 기본값 설정
        link += 'default';
      }

      link += '&checkout=';
   
    
    // arrivalDate에 따라 날짜 코드 설정 (예: 2024-01-30)
    if (arrivalDate) {
        const year = arrivalDate.getFullYear().toString();
        const month = (arrivalDate.getMonth() + 1).toString().padStart(2, '0');
        const day = arrivalDate.getDate().toString().padStart(2, '0');
      
        const formattedArrivalDate = `${year}-${month}-${day}`;
        link += formattedArrivalDate;
      } else {
        // 기본값 설정
        link += 'default';
      }
    // numberOfPeople에 따라 인원 설정
    link += `&adults=${numberOfPeople}&source=structured_search_input_header&search_type=filter_change`;
  
    return link;
  };
  
  export default buildAirbnbLink;


