// NaverTripUtils.js

const buildNaverTripLink = ({ departureCity, arrivalCity, departureDate, arrivalDate, numberOfPeople }) => {
    // 기본 링크
    let link = 'https://flight.naver.com/flights/international/';
  
    // departureCity에 따라 출발지 코드 설정
    const departureMatch = departureCity.match(/, ([^,]+?) (\w+) \|/);
    const departureCode = departureMatch ? departureMatch[2].toUpperCase() : 'default';
    link += `${departureCode}-`;
  
    // arrivalCity에 따라 도착지 코드 설정
    const arrivalMatch = arrivalCity.match(/, ([^,]+?) (\w+) \|/);
    const arrivalCode = arrivalMatch ? arrivalMatch[2].toUpperCase() : 'default';
    link += `${arrivalCode}-`;
  
    // departureDate에 따라 날짜 코드 설정 (예: 20240116)
    if (departureDate) {
      const year = departureDate.getFullYear().toString();
      const month = (departureDate.getMonth() + 1).toString().padStart(2, '0');
      const day = departureDate.getDate().toString().padStart(2, '0');
  
      const formattedDepartureDate = `${year}${month}${day}`;
      link += formattedDepartureDate;
    } else {
      // 기본값 설정
      link += 'default';
    }
    link += '/';
    link += `${arrivalCode}-`;
    link += `${departureCode}`;
    
    // arrivalDate에 따라 날짜 코드 설정 (예: 20240130)
    if (arrivalDate) {
      const year = arrivalDate.getFullYear().toString();
      const month = (arrivalDate.getMonth() + 1).toString().padStart(2, '0');
      const day = arrivalDate.getDate().toString().padStart(2, '0');
  
      const formattedArrivalDate = `${year}${month}${day}`;
      link += `-${formattedArrivalDate}`;
    } else {
      // 기본값 설정
      link += '-default';
    }
  
    // numberOfPeople에 따라 인원 설정
    link += `/?adult=${numberOfPeople}&fareType=YC`;
  
    return link;
  };
  
  export default buildNaverTripLink;


