// SkyscannerUtils.js

const buildSkyscannerLink = ({ departureCity, arrivalCity, departureDate, arrivalDate, numberOfPeople }) => {
    let link = 'https://www.skyscanner.co.kr/transport/flights/';
  
    
   // departureCity에 따라 출발지 코드 설정
   const departureMatch = departureCity.match(/, ([^,]+?) (\w+) \|/);
   const departureCode = departureMatch ? departureMatch[2].toLowerCase() : 'default';
   link += departureCode + '/';

   // arrivalCity에 따라 도착지 코드 설정
   const arrivalMatch = arrivalCity.match(/, ([^,]+?) (\w+) \|/);
   const arrivalCode = arrivalMatch ? arrivalMatch[2].toLowerCase() : 'default';
   link += arrivalCode + '/';
  
    // departureDate에 따라 날짜 코드 설정 (예: 240116)
    if (departureDate) {
      const year = departureDate.getFullYear().toString().slice(2);
      const month = (departureDate.getMonth() + 1).toString().padStart(2, '0');
      const day = departureDate.getDate().toString().padStart(2, '0');
    
      const formattedDepartureDate = `${year}${month}${day}`;
      link += formattedDepartureDate;
      link += '/';
    } else {
      // 기본값 설정
      link += 'default';
    }
  
    if (arrivalDate) {
      const year = arrivalDate.getFullYear().toString().slice(2);
      const month = (arrivalDate.getMonth() + 1).toString().padStart(2, '0');
      const day = arrivalDate.getDate().toString().padStart(2, '0');
    
      const formattedArrivalDate = `${year}${month}${day}`;
      link += formattedArrivalDate;
    } else {
      // 기본값 설정
      link += 'default';
    }
  
    // numberOfPeople에 따라 인원 설정
    link += `/?adults=${numberOfPeople}&adultsv2=${numberOfPeople}&cabinclass=economy&childrenv2=&inboundaltsenabled=false&outboundaltsenabled=false&preferdirects=false&ref=home&rtn=1`;
  
    return link;
  };
  
  export default buildSkyscannerLink;
  


